.detail-info-container {
  padding: 10px;
  background-color: #fff; }
  .detail-info-container .info-container {
    display: flex;
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #f1f1f1; }
  .detail-info-container .info-img-container {
    width: 130px; }
    .detail-info-container .info-img-container img {
      width: 100%; }
  .detail-info-container .info-content {
    margin-left: 10px; }
    .detail-info-container .info-content h1 {
      font-size: 20px;
      color: #333; }
    .detail-info-container .info-content .star-container {
      margin: 5px 0; }
      .detail-info-container .info-content .star-container .price {
        margin-left: 20px;
        color: #999; }
    .detail-info-container .info-content .sub-title {
      color: #999; }
  .detail-info-container .info-desc {
    color: #999;
    line-height: 1.5; }



