.store-buy-container {
  display: flex;
  justify-content: space-between;
  border-top: 1px solid #f1f1f1;
  background-color: #fff;
  padding: 10px 50px; }
  .store-buy-container > div {
    width: 100px;
    padding: 5px 0; }
    .store-buy-container > div span {
      display: block;
      width: 100%;
      height: 100%;
      font-size: 16px;
      color: #666;
      text-align: center;
      border: 1px solid #666;
      background-color: #f1f1f1; }
    .store-buy-container > div .selected {
      color: #e9203d;
      border: 1px solid #e9203d; }



