.list-item {
  display: flex;
  flex-wrap: wrap;
  width: 100%;
  padding: 10px 15px;
  border-bottom: 1px solid #f1f1f1;
  background-color: #fff; }
  .list-item .item-img-container {
    width: 120px;
    height: 90px; }
    .list-item .item-img-container img {
      width: 100%;
      height: 100%; }
  .list-item .item-content {
    flex: 1;
    padding-left: 30px; }
    .list-item .item-content .item-title-container {
      display: flex;
      justify-content: space-between;
      height: 25px; }
      .list-item .item-content .item-title-container h3 {
        font-size: 18px;
        line-height: 1;
        color: #333; }
      .list-item .item-content .item-title-container span {
        font-size: 14px;
        line-height: 18px;
        color: #999; }
    .list-item .item-content .item-sub-title {
      font-size: 14px;
      color: #666;
      line-height: 1.5; }
    .list-item .item-content .item-price-container {
      display: flex;
      justify-content: space-between;
      margin-top: 10px; }
      .list-item .item-content .item-price-container .price {
        font-size: 18px;
        line-height: 1;
        font-weight: 900;
        color: #e9203d; }
      .list-item .item-content .item-price-container .mumber {
        font-size: 14px;
        line-height: 18px;
        color: #999; }



