.login-container {
  width: 300px;
  margin: 160px auto 0 auto; }
  .login-container .input1-container {
    display: flex;
    padding: 5px 10px;
    border: 1px solid #fff;
    border-radius: 5px;
    background-color: #fff;
    overflow: hidden; }
    .login-container .input1-container input {
      display: block;
      height: 20px;
      font-size: 16px;
      border: 0;
      margin-left: 20px !important; }
    .login-container .input1-container i {
      width: 16px;
      margin-top: 3px;
      color: #e9203d; }
    .login-container .input1-container.password-container {
      margin-top: 10px; }
      .login-container .input1-container.password-container button {
        width: 80px;
        height: 20px;
        font-size: 16px;
        color: #e9203d;
        border: 0;
        background-color: #fff;
        text-align: center; }
  .login-container .btn-login {
    width: 100%;
    margin-top: 10px;
    padding: 5px 0;
    text-align: center;
    background-color: #e9203d;
    color: #fff;
    border: 0;
    border-radius: 5px;
    font-size: 16px; }



