@charset "UTF-8";
@import url(../../static/scss/font.css);
body {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1; }
  body * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: '微软雅黑', 'Times New Roman', Georgia, Serif; }

input {
  outline: none; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.search-head {
  display: flex;
  padding: 10px 30px 10px 10px;
  background-color: #e9203d; }

.search-left {
  width: 16px;
  margin: 5px 0;
  text-align: left;
  font-size: 16px;
  color: #fff; }

.input-container {
  flex: 1;
  margin-left: 30px;
  padding: 5px;
  border-radius: 15px;
  background-color: #fff;
  overflow: hidden; }
  .input-container i {
    color: #ccc; }



