@charset "UTF-8";
@import url(../../static/scss/font.css);
body {
  margin: 0;
  padding: 0;
  background-color: #f1f1f1; }
  body * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: '微软雅黑', 'Times New Roman', Georgia, Serif; }

input {
  outline: none; }

ul {
  list-style: none; }

a {
  text-decoration: none; }

.home-header {
  display: flex;
  padding: 10px;
  background-color: #e9203d; }

.home-header-left {
  width: 60px;
  margin: 5px 0;
  text-align: left;
  font-size: 16px;
  color: #fff; }
  .home-header-left span {
    margin-right: 3px; }

.home-header-middle {
  flex: 1; }
  .home-header-middle .search-container {
    padding: 5px;
    border-radius: 15px;
    background-color: #fff;
    overflow: hidden; }
    .home-header-middle .search-container i {
      color: #ccc; }

.home-header-right {
  width: 30px;
  margin: 5px 0;
  text-align: right;
  font-size: 16px;
  color: #fff; }
  .home-header-right span {
    margin-left: 3px; }



